
/*-----------обнуление стилей---------------*/
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
ul,
ol,
li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

html,
body {
  height: 100%;
  font-size: 16px;
  color: #131010;
}
input[type="text"] {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  margin: 0;
}
/*-------------1 блок-------------------*/

.header {
  width: 100%;
  padding: 0;
  margin: 0 auto;
}
.navigation {
  display: flex;
  justify-content: center;
  height: 100%;
  margin: 20px 277px;
}
.link__nav {
  width: 60px;
  height: 60px;
}

.link__img {
  width: 60px;
  height: 60px;
}
.navigation__input {
  width: 550px;
  height: 40px;
  border: 1px solid #bdbdbd;
  border-radius: 7px;
  margin-right: 20px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 14px;
}

.div__input-icon {
  position: relative;
  padding-left: 30px;
  align-items: center;
  display: flex;
}
.div__input-icon::before {
  content: "";
  color: #bdbdbd;
  position: absolute;
  align-items: center;
  bottom: 0;
  margin-left: 5px;
  top: 50%;
  transform: translateY(-50%);
}
.div__input-icon .navigation__input {
  padding-left: 45px;
}
/*------ иконка лупы ------*/
.div__input-icon_search::before {
  width: 32px;
  height: 32px;
  background-image: url("pictures/icon-search.png");
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.4;
}

.navigation__input:focus {
  outline: 0;
  outline-offset: 0;
  color: #212529;
  border: 1px solid #212529;
  box-shadow: 0 0 10px 5px rgba(221, 221, 221, 1);
  cursor: pointer;
}
/*------- кнопка в навигации ---------*/

.button-boards {
  display: block;
  width: 150px;
  height: 40px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 14px;
  color: #212529;
  border-radius: 7px;
  text-align: center;
  position: relative;
  transition: all 0.35s;
  padding-top: 10px;
  background: #bdbdbd;
}
.button-boards_span {
  position: relative;
  z-index: 2;
}
.button-boards::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #ca2d2d;
  border-radius: 7px;
  transition: all 0.35s;
}
.button-boards:hover {
  color: #fff;
}
.button-boards:hover::after {
  width: 100%;
}
.main {
    margin-left: 5%;
    margin-right: auto;
}
.card {
  margin: 10px;
  display: inline-block;
  width: 300px;
  height: 260px;
  position: relative;
  overflow: hidden;
}
.card-content {
  display: flex;
  align-items: center;
  justify-content: end;
  background: #bdbdbd;
  color: #fff;
}
.card-picture {
  width: 100%;
  height: auto;
}

.card-hashtag {
  margin: 10px 0;
}

.card-avatar {
  width: 25px; /* Adjust the size as needed */
  height: 25px; /* Adjust the size as needed */
  border-radius: 50%;
  margin: 10px 5px;
}
//Выбор доски
.Boards__menu{
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 200px;
    border-radius: 15px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
    right: -20px;
}
.Boards__menu a {
    float: none;
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
}
.Boards__menu a:hover {
    background-color: #bdbdbd;
}
.div__button:hover .Boards__menu {
    display: block;
}
//Меню при наведении
.pin-menu {
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(128, 128, 128, 0.63);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.5s ease;
  }

  .card:hover .pin-menu {
    opacity: 1;
  }
  
  .pin-menu__button {
    margin-bottom: 15px;
    display: block;
    width: 150px;
    height: 40px;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
    font-size: 14px;
    color: white;
    border-radius: 7px;
    text-align: center;
    background: #ca2d2d;
  }
  
  .pin-menu__button:hover {
    cursor: pointer;
  }
//Модальные окна
.modal-add{
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  top: 0;
  left: 0;
  z-index: 11;
}
.modal-add__container {
  min-height: 100%;
  max-width: 800px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 10px;
}
.modal-add__boards {
  background-color: white;
  color: black;
  width: 500px;
  padding: 30px;
  border-radius: 20px;
}
.modal-add__title {
  font-size: 40px;
  font-weight: 700;
  margin:0px 0px 40px 0px ;
  text-align: center;
}
.modal-add__list {
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;
}
.list__item{
  float: none;
  color: black;
  font-size: 30px;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: center;
}
.list__item:hover {
  color: red;
}
.modal-add__footer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.modal-add__close {
  width: 130px;
  height: 60px;
  text-align: center;
  font-size: 20px;
  border-radius: 40px;
  font-weight: 800;
  border: 0;
  cursor: pointer;
}
.modal-add__close:hover {
  color: red;
}
.modal-add__submit {
  width: 130px;
  height: 60px;
  text-align: center;
  font-size: 20px;
  border-radius: 40px;
  font-weight: 800;
  border: 0;
  margin-left: 20px;
}
//Модальное окно 
.modal{
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    top: 0;
    left: 0;
    z-index: 12;
}

.modal_body {
    min-height: 100%;
    max-width: 800px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px 10px;
}
.modal_content {
    background-color: white;
    color: black;
    width: 800px;
    padding: 30px;
    border-radius: 20px;
}
.modal_title {
    font-size: 40px;
    margin:0px 0px 50px 0px ;
    text-align: center;
}
.modal_text {
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
}
.modal_label {
    cursor: pointer;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: flex-end;
    margin-bottom: 30px;
    margin-left: 30px;
}
.modal_checkbox {
   width: 0;
   height: 0;
   position: absolute;
   z-index: -1;
   opacity: 0;
}
.modal_checkbox:focus +.modal_span {
    box-shadow: 0 0 0 1px red , 0 0 10px red;
    
}
.modal_span {
    display: inline-block;
    position: relative;
    width: 20px;
    height: 20px;
    border: 2px solid #3d3b3b;
    background: white;
    vertical-align: sub;
    border-radius: 17px;
    margin-right: 20px;
}
.modal_span::before {
    content: '';
    display: inline-block;
    position: absolute;
    z-index: 10;
    top: 15%;
    left: 30%;
    width: 5px;
  height: 10px;
  border: solid #f62012;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg) scale(0);
}
.modal_checkbox:checked +  .modal_span::before {
    transform: rotate(45deg) scale(1);
    
}
.modal_footer {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.modal_close {
    width: 130px;
    height: 60px;
    text-align: center;
    font-size: 20px;
    border-radius: 40px;
    font-weight: 800;
    border: 0;
    cursor: pointer;
}
.modal_submit {
    width: 130px;
    height: 60px;
    text-align: center;
    font-size: 20px;
    border-radius: 40px;
    font-weight: 800;
    border: 0;
    color: #838181;
    margin-left: 20px;
}  